import http from './http'

export default {
  login: (params) => http.post('/user/login', params),
  register: (params) => http.post('/user/register', params),
  /**
   * 
   * @param {*} params 
   * @returns 
   */
  addOrder: (params) => http.post('/order', params),
  /**
   * 
   * @param {*} params 
   * @returns 
   */
   modifyOrder: (params) => http.put('/order', params),
  /**
   * 
   * @param {*} params 
   * @returns 
   */
  getOrder: (params) => http.get('/order', params),
  /**
   * 
   * @param {Array} params 
   * @returns 
   */
  midSettle: (params) => http.get('/midSettle', params),
  /**
  * 
  * @param {Array} params 
  * @returns 
  */
  submitSettle: (params) => http.get('/submitSettle', params),
  /**
   * 
   * @param {*} params 
   * @returns 
   */
  changeOrderStatus: (params) => http.put('/order/status', params),

  /**
   * 
   * @param {*} params 
   * @returns 
   */
  searchOrder: (params) => http.get('/order/search', params),



  
}

